import React, { useEffect, useState } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import PlayersTab from '../Tabs/PlayersTab/PlayersTab';
import TeamsTab from '../Tabs/TeamsTab/TeamsTab';
import ForumTab from '../Tabs/ForumTab/ForumTab';
import DraftTab from '../Tabs/DraftTab/DraftTab';
import RulesTab from '../Tabs/RulesTab/RulesTab';
import AdminTab from '../Tabs/AdminTab/AdminTab';
import PickTrackerTab from '../Tabs/PickTrackerTab/PickTrackerTab';
import Emoji from '../Emoji';
import { updateUrlPath } from '../../../../src/util/util';
import { useLocation, Route, Link } from "react-router-dom";
import './Navbar.css';

export default function Navbar({
  currentPick, setCurrentPick, picks, setPicks, draftingNow, setDraftingNow, userId, 
  sendChatAnnouncement, players, setPlayers, goalies, setGoalies,
  teams, setTeams, posts, setPosts, rules, setRules, user, getLatestData
}) {
  const pathToIndexMap = {
    '/draft': 0,
    '/skaters': 1,
    '/goalies': 2,
    '/teams': 3,
    '/forum': 4,
    '/rules': 5,
    '/pick-tracker': 6,
    '/admin': 7
  }
  const location = useLocation();
  const defaultIndex = pathToIndexMap[location.pathname] || 0;
  console.log(`defaultIndex: ${JSON.stringify(defaultIndex, null, 4)}`);
  const [updateTab, setUpdateTab] = useState(null);

  useEffect(() => {
    if (updateTab) {
      const pathWithoutParams = updateTab.match(/^[^?]*/);
      const tab = pathToIndexMap[`/${pathWithoutParams}`];
      const tabId = `react-tabs-${tab * 2}`; // x2 because react-tabs uses an extra hidden tab for each for a11y purposes

      document.getElementById(tabId).click();
      updateUrlPath(updateTab);
      setUpdateTab(null);
    }
  }, [updateTab, pathToIndexMap])

  return (
    <>
      <Tabs defaultIndex={defaultIndex} className="navbar-tabs">
        <TabList>
          <Tab>
            <Link to='/draft'>
              <Emoji navbar={true} emoji=''  />
              <div>Draft</div>
            </Link>
          </Tab>
          <Tab>
            <Link to='/skaters'>
              <Emoji navbar={true} emoji='' />
              <div >Skaters</div>
            </Link>
          </Tab>
          <Tab>
            <Link to='/goalies'>
              <Emoji navbar={true} emoji='' />
              <div >Goalies</div>
            </Link>
          </Tab>
          <Tab>
            <Link to='/teams'>
              <Emoji navbar={true} emoji='' />
              <div>Teams</div>
            </Link>
          </Tab>
          <Tab>
            <Link to='/forum'>
              <Emoji navbar={true} emoji='' />
              <div>Forum</div>
            </Link>
          </Tab>
          <Tab>
            <Link to='/rules'>
              <Emoji navbar={true} emoji='' />
              <div>Rules</div>
            </Link>
          </Tab>
          <Tab>
            <Link to='/pick-tracker'>
              <Emoji navbar={true} emoji='' />
              <div>Pick Tracker</div>
            </Link>
          </Tab>
          { user.role === 'admin' && (
            <Tab>
              <Link to='/admin'>
                <Emoji navbar={true} emoji='' />
                <div>Admin</div>
              </Link>
            </Tab>
          )}
        </TabList>
        <switch>

        <TabPanel>
          <Route to='/draft'>
            <DraftTab 
              user={user}
              currentPick={currentPick}
              setCurrentPick={setCurrentPick}
              draftingNow={draftingNow}
              setDraftingNow={setDraftingNow}
              userId={userId}
              picks={picks}
              setPicks={setPicks}
              setTeams={setTeams}
              setPlayers={setPlayers}
              setGoalies={setGoalies}
              getLatestData={getLatestData}
              sendChatAnnouncement={sendChatAnnouncement}
              setUpdateTab={setUpdateTab}
            />
          </Route>
        </TabPanel>
        <TabPanel>
          <Route to='/skaters'>
            <PlayersTab
              playerType='skaters'
              user={user}
              setPicks={setPicks}
              setCurrentPick={setCurrentPick}
              draftingNow={draftingNow}
              setDraftingNow={setDraftingNow}
              sendChatAnnouncement={sendChatAnnouncement}
              players={players}
              setPlayers={setPlayers}
              setGoalies={setGoalies}
              setTeams={setTeams}
              getLatestData={getLatestData}
              currentPick={currentPick}
            />
          </Route>
        </TabPanel>
        <TabPanel>
          <Route to='/goalies'>
            <PlayersTab
              playerType='goalies'
              user={user}
              setPicks={setPicks}
              setCurrentPick={setCurrentPick}
              draftingNow={draftingNow}
              setDraftingNow={setDraftingNow}
              sendChatAnnouncement={sendChatAnnouncement}
              players={goalies}
              setPlayers={setPlayers}
              setGoalies={setGoalies}
              setTeams={setTeams}
              getLatestData={getLatestData}
              currentPick={currentPick}
            />
          </Route>
        </TabPanel>
        <TabPanel>
          <Route to='/goalies'>
            <TeamsTab 
              user={user}
              teams={teams}
              setTeams={setTeams}
              getLatestData={getLatestData}
            />
          </Route>
        </TabPanel>
        <TabPanel>
          <Route to='/goalies'>
            <ForumTab 
              user={user}
              posts={posts}
              setPosts={setPosts}
              getLatestData={getLatestData}
              setUpdateTab={setUpdateTab}
            />
          </Route>
        </TabPanel>
        <TabPanel>
          <Route to='/goalies'>
            <RulesTab
              user={user}
              rules={rules}
              setRules={setRules}
              getLatestData={getLatestData}
            />
          </Route>
        </TabPanel>
        <TabPanel>
          <Route to='/goalies'>
            <PickTrackerTab />
          </Route>
        </TabPanel>
        { user.role === 'admin' && (
          <TabPanel>
            <Route to='/goalies'>
              <AdminTab />
            </Route>
          </TabPanel>
          )
        }
        </switch>
      </Tabs>
    </>
  );
}

